/* Modal Styles */
.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent overlay */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 999; /* Ensure the modal appears on top */
    opacity: 0; /* Start with opacity at 0 for the fade-in effect */
    transition: opacity 0.5s ease-in-out; /* Transition for opacity change */
  }

  .modal-overlay.visible {
    opacity: 1; /* When the modal is visible, change opacity to 1 */
  }

  
  .modal {
    display: flex;
    background-color: white;
    padding: 32px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
    width: 80%;
    max-width: 500px;
    align-items: center;
    position: relative;
  }

  .close {
    position: absolute;
    top: 48px;
    right: 32px;
    cursor: pointer;
  }

  